import React, { useState } from 'react'
import '../../css/signIn/signInForm.css'
import '../../css/signIn/signUpForm.css'

import newUserSignUp from '../../auth/signUp'

function SignUpForm({ lang }) {
    const [user, setUser] = useState({})

    let signUp
    let email
    let password

    const createUser = (e) => {
        e.preventDefualt()
        newUserSignUp(user.email, user.password)
    }

    if (lang === 'English') {
        signUp = 'Sign Up'
        email = 'Email Address'
        password = 'Password'
    } else {
        signUp = 'Suscríbete'
        email = 'Email'
        password = 'Contraseña'
    }

    return (
        <div className="form-container2">
            <form className="signUp-form" onSubmit={createUser}>
                <h1>{signUp}</h1>
                <input
                    type="email"
                    placeholder={email}
                    onChange={(e) =>
                        setUser({ ...setUser, email: e.target.value })
                    }
                />
                <input
                    type="password"
                    placeholder={password}
                    onChange={(e) =>
                        setUser({ ...setUser, password: e.target.value })
                    }
                />
                <button>{signUp}</button>
            </form>
        </div>
    )
}

export default SignUpForm
